import indexUrl from '../routes/indexUrl';
import urlParse from '../routes/urlParse';

class App {
  constructor({ content }) {
    this._content = content;
  }

  async renderPage() {
    const url = urlParse.parseUrl();
    const page = indexUrl[url];
    this._content.innerHTML = await page.render();
    await page.afterRender();
    // console.log(url);
  }
}

export default App;
