import apiConfig from '../../config/apiConfig';

class MoviesDB {
  static async Upcomming(page = '1', MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getUpcommingMoviesOrTv(page, MovieOrTv));
    const responseJson = await response.json();
    const data = [];
    if (MovieOrTv === 'movie') {
      responseJson.results.forEach((movie) => {
        movie.media = 'movie';
        data.push(movie);
      });
      // console.log(data);
      return responseJson.results;
      // return data;
    }
    responseJson.results.forEach((tv) => {
      tv.media = 'tv';
      data.push(tv);
    });
    // console.log(data);
    return responseJson.results;
    // return data;
  }

  static async Trending(page = '1', MoviesOrTv = 'movie') {
    const response = await fetch(apiConfig.getTrendingMoviesOrTv(page, MoviesOrTv));
    const responseJson = await response.json();
    const data = [];
    if (MoviesOrTv === 'movie') {
      responseJson.results.forEach((movie) => {
        movie.media = 'movie';
        data.push(movie);
      });
      // console.log(data);
      return data;
    }
    responseJson.results.forEach((tv) => {
      tv.media = 'tv';
      data.push(tv);
    });
    // console.log(data);
    return data;
  }

  static async Latest(MoviesOrTv = 'movie') {
    const response = await fetch(apiConfig.getLatestMoviesOrTv(MoviesOrTv));
    const responseJson = await response.json();
    if (MoviesOrTv === 'movie') {
      responseJson.latest = 'true';
      return responseJson;
    }
    responseJson.latest = 'true';
    return responseJson;
  }

  static async Genre(MovieOrTv = 'movie') {
    const response1 = await fetch(apiConfig.getGenre(MovieOrTv));
    const responseJson1 = await response1.json();
    const response2 = await fetch(apiConfig.getGenre('tv'));
    const responseJson2 = await response2.json();
    const data = responseJson1.genres;
    responseJson2.genres.map((x) => {
      if (!responseJson1.genres.some((i) => i.name === x.name)) {
        data.push(x);
      }
    });
    return data;
  }

  static async MovieOrTvByGenre(id, page = '1', MovieOrTv = 'movie') {
    const response1 = await fetch(apiConfig.getMovieOrTvByGenre(id, page, MovieOrTv));
    const responseJson1 = await response1.json();
    const response2 = await fetch(apiConfig.getMovieOrTvByGenre(id, page, 'tv'));
    const responseJson2 = await response2.json();
    const data = [];
    responseJson1.results.forEach((movie) => {
      movie.media = 'movie';
      movie.dateToSort = movie.release_date;
      data.push(movie);
    });
    responseJson2.results.forEach((tv) => {
      tv.media = 'tv';
      tv.dateToSort = tv.first_air_date;
      data.push(tv);
    });
    // console.log(data.sort((a, b) => ((a.dateToSort < b.dateToSort) ? 1 : (a.dateToSort > b.dateToSort) ? -1 : 0)));
    return data.sort((a, b) => ((a.dateToSort < b.dateToSort) ? 1 : (a.dateToSort > b.dateToSort) ? -1 : 0));
  }

  static async spesificGenre(idGenre, MovieOrTv = 'movie') {
    const response1 = await fetch(apiConfig.getGenre(MovieOrTv));
    const responseJson1 = await response1.json();
    const response2 = await fetch(apiConfig.getGenre('tv'));
    const responseJson2 = await response2.json();
    let genres = '';
    responseJson1.genres.forEach((genre) => (genre.id == idGenre ? genres = genre.name : ''));
    responseJson2.genres.forEach((genre) => (genre.id == idGenre ? genres = genre.name : ''));
    return genres;
  }

  static async Trailer(id, MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getTrailler(id, MovieOrTv));
    const responseJson = await response.json();
    // console.log(responseJson.results);
    return responseJson.results.filter((trailer) => trailer.type === 'Trailer' || trailer.type === 'Clip');
  }

  static async Actor() {
    const response = await fetch(apiConfig.getPopularActor);
    const responseJson = await response.json();
    const data = [];
    responseJson.results.forEach((person) => {
      person.media = 'person';
      data.push(person);
    });
    // console.log(data);
    return data;
  }

  static async DetailActor(id) {
    const response = await fetch(apiConfig.getDetailActor(id));
    const responseJson = await response.json();
    return responseJson;
  }

  static async MoviesOrTvByActor(id, MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getMoviesOrTvByActor(id, MovieOrTv));
    const responseJson = await response.json();
    const data = [];
    if (MovieOrTv === 'movie') {
      responseJson.cast.forEach((movie) => {
        movie.media = 'movie';
        data.push(movie);
      });
      // console.log(data);
      return data;
    }
    responseJson.cast.forEach((tv) => {
      tv.media = 'tv';
      data.push(tv);
    });
    // console.log(data);
    return data;
  }

  static async MoviesNowPlaying(page = '1') {
    const response = await fetch(apiConfig.getNowPlayingMovies(page));
    const responseJson = await response.json();
    const data = [];
    responseJson.results.forEach((movie) => {
      movie.media = 'movie';
      data.push(movie);
    });
    return data;
  }

  static async Detail(id, MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getDetailMovieOrTv(id, MovieOrTv));
    const responseJson = await response.json();
    if (MovieOrTv === 'movie') {
      responseJson.media = 'movie';
      return responseJson;
    }
    responseJson.media = 'tv';
    return responseJson;
  }

  static async Recommendation(id, MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getRecommendationMoviesOrTv(id, MovieOrTv));
    const responseJson = await response.json();
    const data = [];
    if (MovieOrTv === 'movie') {
      responseJson.results.forEach((movie) => {
        movie.media = 'movie';
        data.push(movie);
      });
      // console.log(data);
      return data;
    }
    responseJson.results.forEach((tv) => {
      tv.media = 'tv';
      data.push(tv);
    });
    // console.log(data);
    return data;
  }

  static async Similar(id, MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getSimilarMoviesOrTv(id, MovieOrTv));
    const responseJson = await response.json();
    const data = [];
    if (MovieOrTv === 'movie') {
      responseJson.results.forEach((movie) => {
        movie.media = 'movie';
        data.push(movie);
      });
      // console.log(data);
      return data;
    }
    responseJson.results.forEach((tv) => {
      tv.media = 'tv';
      data.push(tv);
    });
    // console.log(data);
    return data;
  }

  static async Search(query) {
    const response = await fetch(apiConfig.searchMoviesOrTv(query));
    const responseJson = await response.json();
    return responseJson.results;
  }

  static async CreditMovieOrTv(id, MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getCastByMovieOrTv(id, MovieOrTv));
    const responseJson = await response.json();
    responseJson.cast.forEach((person) => {
      person.media = 'person';
    });
    return responseJson.cast;
  }

  static async AiringToday(page = '1') {
    const response = await fetch(apiConfig.getTvAiringtoday(page));
    const responseJson = await response.json();
    responseJson.results.forEach((tv) => {
      tv.media = 'tv';
    });
    return responseJson.results;
  }

  static async TvOnAir(page = '1') {
    const response = await fetch(apiConfig.getTvAiringtoday(page));
    const responseJson = await response.json();
    responseJson.results.forEach((tv) => {
      tv.media = 'tv';
    });
    return responseJson.results;
  }

  static async SeasonDetail(id, seasonNumber) {
    const response = await fetch(apiConfig.getDetailSeason(id, seasonNumber));
    const responseJson = await response.json();
    // console.log(responseJson);
    return responseJson;
  }

  static async EpisodeDetail(id, seasonNumber, episodeNumber) {
    const response = await fetch(apiConfig.getDetailEpisode(id, seasonNumber, episodeNumber));
    const responseJson = await response.json();
    return responseJson;
  }

  static async TopRated(page = '1', MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getTopRated(page, MovieOrTv));
    const responseJson = await response.json();
    if (MovieOrTv === 'tv') {
      responseJson.results.map((tv) => {
        tv.media = 'tv';
      });
      return responseJson.results;
    }
    responseJson.results.map((movie) => {
      movie.media = 'movie';
    });
    return responseJson.results;
  }

  static async PopularMovieOrTv(page = '1', MovieOrTv = 'movie') {
    const response = await fetch(apiConfig.getPopularMovieOrTv(page, MovieOrTv));
    const responseJson = await response.json();
    if (MovieOrTv === 'tv') {
      responseJson.results.map((tv) => {
        tv.media = 'tv';
      });
      return responseJson.results;
    }
    responseJson.results.map((movie) => {
      movie.media = 'movie';
    });
    return responseJson.results;
    // console.log(responseJson.results);
  }
}

export default MoviesDB;
